/**
 * @module widget-bbm-personal-profile-ng
 *
 * @description
 * Personal profile.
 *
 * @example
 *  <div ng-controller="PersonalProfileController as $ctrl">
 *    <ul>
 *       <li>{{ ext.helpers.getProcessedUser().firstName }}</li>
 *    </ul>
 *  </div>
 */
import angular from 'vendor-bb-angular';
import widgetModuleKey, { widgetKey } from 'lib-bb-widget-ng';
import bbIntentModuleKey, { bbIntentKey } from 'lib-bb-intent-ng';
import stateContainerModuleKey, { bbStateContainerKey } from 'lib-bb-state-container-ng';

import eventBusModuleKey, {
  eventBusKey,
} from 'lib-bb-event-bus-ng';
import bbExtensionHelpersModuleKey, {
  extensionHelpersContextKey,
} from 'lib-bb-extension-helpers-ng';
import bbExtensionEventsModuleKey, {
  extensionEventsContextKey,
} from 'lib-bb-extension-events-ng';
import bbExtensionIntentsModuleKey, {
  extensionIntentsContextKey,
} from 'lib-bb-extension-intents-ng';

import modelPersonalProfileModuleKey, {
  modelPersonalProfileKey,
} from 'model-bb-personal-profile-ng';

import PersonalProfileController from './controller';
import PersonalProfileFormController from './form-controller';
import createViewModel from './view-model';
import createService from './service';
import widgetEvents from './events';
import { Event } from './constants';

const moduleKey = 'widget-bbm-personal-profile-ng';
const eventServiceKey = `${moduleKey}:events`;
const serviceKey = `${moduleKey}:service`;
const viewModelKey = `${moduleKey}:viewModel`;

export default angular
  .module(moduleKey, [
    modelPersonalProfileModuleKey,
    eventBusModuleKey,
    widgetModuleKey,
    bbIntentModuleKey,
    stateContainerModuleKey,
    bbExtensionHelpersModuleKey,
    bbExtensionEventsModuleKey,
    bbExtensionIntentsModuleKey,
  ])
  .factory(eventServiceKey, [
    eventBusKey,
    /* into */
    widgetEvents,
  ])
  .factory(viewModelKey, [
    bbStateContainerKey,
    /* into */
    createViewModel,
  ])
  .factory(serviceKey, [
    modelPersonalProfileKey,
    viewModelKey,
    bbStateContainerKey,
    eventServiceKey,
    /* into */
    createService,
  ])
  .factory(extensionEventsContextKey, [
    bbIntentKey,
    serviceKey,
    (bbIntent, service) => ({ bbIntent, service }),
  ])
  .factory(extensionHelpersContextKey, [
    bbIntentKey,
    serviceKey,
    (bbIntent, service) => ({ bbIntent, service }),
  ])
  .factory(extensionIntentsContextKey, [
    serviceKey,
    (service) => ({ service }),
  ])
  .controller('PersonalProfileController', [
    serviceKey,
    /* into */
    PersonalProfileController,
  ])
  .controller('PersonalProfileFormController', [
    /* into */
    PersonalProfileFormController,
  ])
  .run([
    bbIntentKey, eventBusKey, viewModelKey, widgetKey,
    (bbIntent, eventBus, viewModel, widget) => {
      viewModel.init();

      bbIntent.init().then(() => {
        eventBus.publish(Event.CXP_ITEM_LOADED, {
          id: widget.getId(),
        });

        eventBus.publish(Event.BB_ITEM_LOADED, {
          id: widget.getId(),
        });
      });
    }])
  .name;




