import createInitialState from './initial-state';
import createUserReducer from './user';
import createFormReducer from './form';
import createSelectedEmailReducer from './selected-email';
import createSelectedPhoneReducer from './selected-phone';

export default stateContainer => {
  const initialState = createInitialState();

  /**
   * @name ViewModel
   * @type {object}
   * @ngKey widget-bbm-personal-profile-ng:viewModel
   * @inner
   */
  return {
    /**
     * @name ViewModel#init
     * @description Initialises view model with initial state.
     * @type {function}
     *
     * @return {ViewState}
     * @inner
     */
    init: stateContainer.createAction((state = initialState) => state),

    /**
     * @name ViewModel#getState
     * @description Returns view state
     * @type {function}
     *
     * @return {ViewState}
     * @inner
     */
    getState: stateContainer.getState,

    /**
     * @name ViewModel#user
     * @description
     * Actions for updating the parts of the `ViewState` related to the user.
     *
     * @type {function}
     * @return {ViewState}
     * @inner
     */
    user: createUserReducer(stateContainer),

    /**
     * @name ViewModel#form
     * @description
     * Actions for updating the parts of the `ViewState` related to the form.
     *
     * @type {function}
     * @return {ViewState}
     * @inner
     */
    form: createFormReducer(stateContainer),

    /**
     * @name ViewModel#selectEmail
     * @description
     * Actions for updating the parts of the `ViewState` related to the selected email.
     *
     * @type {function}
     * @return {ViewState}
     * @inner
     */
    selectedEmail: createSelectedEmailReducer(stateContainer),

    /**
     * @name ViewModel#selectPhone
     * @description
     * Actions for updating the parts of the `ViewState` related to the selected phone.
     *
     * @type {function}
     * @return {ViewState}
     * @inner
     */
    selectedPhone: createSelectedPhoneReducer(stateContainer),
  };
};



