import { uiError } from './helpers';

/**
 * @name ViewModelUserActions
 * @type {object}
 * @inner
 */
export default stateContainer => ({
  /**
   * @name ViewModelUserActions#beforeUserLoad
   * @description Update the `ViewState` before a user is loaded.
   * @type {function}
   *
   * @return {void}
   * @inner
   */
  beforeUserLoad: stateContainer.createAction(state => ({
    ...state,
    user: {
      ...state.user,
      loading: true,
    },
  })),

  /**
   * @name ViewModelUserActions#beforeUserUpdate
   * @description Update the `ViewState` before a user is submitted for update.
   * @type {function}
   *
   * @return {void}
   * @inner
   */
  beforeUserUpdate: stateContainer.createAction(state => ({
    ...state,
    user: {
      ...state.user,
      loading: true,
    },
  })),

  /**
   * @name ViewModelUserActions#afterUserLoadSuccess
   * @description Update the `ViewState` after the user is successfully loaded.
   * @type {function}
   *
   * @param {module:model-bb-personal-profile-ng.UserProfile} user
   * @return {void}
   * @inner
   */
  afterUserLoadSuccess: stateContainer.createAction((state, user) => ({
    ...state,
    user: {
      ...state.user,
      data: user,
      loading: false,
      error: null,
    },
  })),

  /**
   * @name ViewModelUserActions#afterUserUpdateSuccess
   * @description Update the `ViewState` after the user is updated.
   * @type {function}
   *
   * @param {module:model-bb-personal-profile-ng.UserProfile} user
   * @return {void}
   * @inner
   */
  afterUserUpdateSuccess: stateContainer.createAction((state, user) => ({
    ...state,
    user: {
      ...state.user,
      data: user,
      loading: false,
      error: null,
    },
  })),

  /**
   * @name ViewModelUserActions#afterUserLoadError
   * @description Update the `ViewState` after unsuccessful user load.
   * @type {function}
   *
   * @param {module:model-bb-personal-profile-ng.ModelError} error Response error
   * @return {void}
   * @inner
   */
  afterUserLoadError: stateContainer.createAction((state, error) => ({
    ...state,
    user: {
      ...state.user,
      loading: false,
      error: uiError(error),
    },
  })),

  /**
   * @name ViewModelUserActions#afterUserUpdateError
   * @description Update the `ViewState` after unsuccessful user update.
   * @type {function}
   *
   * @param {module:model-bb-personal-profile-ng.ModelError} error Response error
   * @return {void}
   * @inner
   */
  afterUserUpdateError: stateContainer.createAction((state, error) => ({
    ...state,
    user: {
      ...state.user,
      loading: false,
      error: uiError(error),
    },
  })),
});



